import React, { Component } from 'react'
import Header from './Header'
import Analyze from './Analyze'
import Footer from '../Footer'
import Results from '../Results'
import Spinner from '../Spinner'
import store from '../Store'
import { API } from '../../Constants'
import { Redirect } from 'react-router-dom'

class Detail extends Component {

  constructor(props) {
    super(props);
  }

  state = {
    working: false,
    problem: false,
    error: "Unable analyze your files",
    newImageSet: "",
    reprocessed: false
  }

  reprocess = (imageSetParam) => {
    console.log("reprocess = (imageSetParam)", imageSetParam, this.state.working);
    if (this.state.working) {
      return;
    }
    let that = this,
      imageSet = imageSetParam ? imageSetParam : store.user().imageSet;
    let images = store.images(imageSet), urls = [];
    if (images == null || images.length == 0) {
      //that.setState({ reprocessed: true, imageSet: imageSet });
      return;
    }
    this.setState({ working: true });
    for (let i = 0; i < images.length; i++) {
      let img = images[i];
      urls.push(img.Url);
    }

    let data = {
      "Urls": urls,
      "PublishEnvironment": store.user().environment //TODO: Decide by login
    }

    if (store.hasUser()) {
      data["AudienceSize"] = store.user().fanCount;
    }

    fetch(API + '/page/sortimagesurls', {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8"
      },
      body: JSON.stringify(data)
    }).then(function (response) {
      if (response.ok) {
        return response.json();
      }
      return false;
    }).then(function (data) {

      if (data && data.length > 0) {
        let newImageSet = that.clientId();
        //console.log("response data", imageSet, JSON.stringify(store.getState()));
        store.images(newImageSet, data);
        let u = store.user();
        u.imageSet = newImageSet;
        u.shouldReprocess = false;
        store.user(u);
        setTimeout(function () {
          this.setState({ reprocessed: true, newImageSet: newImageSet, working: false });
        }.bind(that),
          500);
      } else {
        //that.setState({ reprocessed: true, newImageSet: imageSet, working: false, problem: true });
      }
    }).catch(function (error) {
      console.log('request failed', error)
    });
  }

  clientId = () => {
    return Math.random().toString(36).slice(2);
  }

  componentDidMount() {
    this.componentWillReceiveProps(this.props);
  }

  componentWillReceiveProps = (nextProps) => {
    const { match } = nextProps;
    if (match.params.images == "reprocess") {
      setTimeout(function () {
        this.reprocess();
      }.bind(this), 100);
    } else if (match.params.selected == "reprocess") {
      setTimeout(function (param) {
        this.reprocess(param);
      }.bind(this)(match.params.images), 100);
    }

    if (this.state.reprocessed) {
      //console.log("this.state.reprocessed", this.state.reprocessed);
      this.setState({ reprocessed: false });
    }
  }

  render() {
    const { match } = this.props;
    const { reprocessed, working, problem, error, newImageSet } = this.state
    let selection = match.params.selected;
    //console.log("render", match.params.images, match.params.selected);
    if (selection === undefined || selection == null || selection == "reprocess") {
      let imageSetParam = match.params.images, images = store.images(imageSetParam);
      if (images != null && images.length > 0) {
        let best = images[0];
        for (let i = 0; i < images.length; i++) {
          if (best.Result.Rank < images[i].Result.Rank) {
            best = images[i];
          }
        }
        selection = best.Id;
      }
    } else {
      if (selection == "reprocess") {
        selection = undefined;
      }
    }
    return (
      <div>
        {reprocessed && (
          <Redirect to={"/detail/" + newImageSet} />
        )}
        <Header />
        {working && (
          <div className="reprocess" style={{ textAlign: "center" }}>
            <span >Analyzing your pictures please wait.</span>
            <Spinner />
          </div>
        )}
        <Analyze imageSet={match.params.images} selected={selection} />
        <div className="container">
          <div className="row">
            <div className="col">
              <h2>Other Evaluated Images &nbsp;&nbsp;<span style={{ fontSize: "12px", fontStyle: "italic" }}>Click to see details</span></h2>
            </div>
          </div>
          <Results howMany={3} imageSet={match.params.images} selected={selection} />
        </div>
        <Footer />
      </div>
    )
  }
}

export default Detail




